.bingkai-promosi{
    height: 100vh;
    margin: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10rem;
}

.promosi{
    background-color: #ffffff;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 15rem;
    padding: 10rem;
    border-radius: 20px;
    width: 100%;
}

.gambar-promosi img{
    width: 25rem;
    height: 25rem;
}

.diskripsi-promosi{
    max-width: 50rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 10px;
    color: #000000;
}

.diskripsi-promosi h2{
    padding-bottom: 1rem;
    font-size: 3em;
}

.diskripsi-promosi p{
    font-size: 1.5em;
    padding-bottom: 2rem;
}

.diskripsi-promosi span{
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 20px;
}

.diskripsi-promosi img{
    width: 1.8rem;
    transform: scalex(-1);
}

@media screen and (max-width: 570px){

    .bingkai-promosi{
        width: 20rem;
        padding: 10px;
    }
    .promosi{
        flex-direction: column;
        padding: 1rem;
        gap: 1rem;
    }
    .gambar-promosi img{
        width: 10rem;
        height: 10rem;
    }
    .diskripsi-promosi p{
        font-size: 0.8em;
        padding-bottom: 2rem;
    }
    .diskripsi-promosi h2{
        padding-bottom: 1rem;
        font-size: 1.5em;
    }
    .diskripsi-promosi h3{
        padding-bottom: 1rem;
        font-size: 0.8em;
        padding: 3px;
    }

    .diskripsi-promosi img{
        width: 1.2rem;
        transform: scalex(-1);
    }
}