*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body{
    background-color: #f7f7f7;
}

.hero{
    height: 80vh;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    height: 90vh;
    background-color: #f7f7f7;
}

.hero h1{
    font-size: 4em;
}

.hero p{
    font-size: 1.2em;
    margin-top: 10px;
}

.hero button{
    margin-top: 20px;
    border: none;
    width: 10rem;
    height: 2.5rem;
    border-radius: 10px;
    background-color: #000000;
    color: #ffffff;
    transition: background-color 0.2s;
}

.hero button:hover{
    background-color: #FD0D16;
    color: #ffffff;
}

.hero img{
    width: 20rem;
    transform: scaleX(-1);
}

@media screen and (max-width: 760px){
    .hero{
        flex-direction: column-reverse;
    }

    .hero img{
        width: 13rem;
    }

    .hero h1{
        font-size: 2.5em;
    }
    
    .hero p{
        font-size: 1em;
    }
}

@media screen and (max-width:1080px){
    
    .hero{
        margin: 20px;
        
    }
    
    .hero img{
        width: 15rem;
    }


    .hero h1{
        font-size: 2em;
    }

    .hero p {
        font-size: 1em;
    }
}

@media screen and (max-width: 570px){
    .hero{
        flex-direction: column-reverse;
        margin: 20px;
    }

    .hero img{
        width: 13rem;
    }

    .hero h1{
        font-size: 2.5em;
    }
    
    .hero p{
        font-size: 1em;
    }
}