footer{
    background-color: #01131bd8;
    color: #ffffff;   
    display: flex;
    justify-content: space-between;
    padding: 10rem;
    align-items: center;
    height: 100%;
    gap: 40px;
    font-family: Arial, Helvetica, sans-serif;
}

.logo-footer{
    color: #ffffff;
}

.logo-footer h3{
    font-size: 3em;
}

.middle{
    font-size: 1.2em;
    width: 25rem;
}

.middle ul{
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
}

.middle ul li{
    padding: 10px;
}

.rightf{
    display: flex;
    justify-content: center;
    flex-direction: column;
    gap: 20px;
}

.rightf a{
    display: flex;
    align-items: center;
}


.rightf span{
    text-decoration: none;
    color: #ffffff;
    padding: 1rem; 
}

.rightf img{
    width: 2rem;
    
}


@media screen and (max-width: 570px) {
    footer {
        flex-direction: column;
        padding: 2rem;
        justify-content: flex-start; /* Mengubah dari space-between menjadi flex-start */
    }

    .middle {
        width: 100%;
        text-align: left; /* Mengubah dari center menjadi left */
    }

    .middle ul li {
        padding: 5px;
    }

    .rightf {
        margin-top: 20px;
        text-align: left; /* Mengubah dari center menjadi left */
    }

    .logo-footer h3 {
        font-size: 2em;
    }

    .rightf img {
        width: 1.5rem;
    }
}
